header {
  width: 100%;
  position: fixed;
  top: 0;
  right: 0;
  font-size: 1em;
  padding: 0px 20px;
}
header ul {
  float: right;
}
header li {
  float: left;
  margin-left: 0.2em;
  padding: 10px 12px;
}
header li a {
  color: #383838;
}
header li a:hover {
  color: #000;
}


